import React, { useContext, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Login } from "../../ShareData/Login";

export default function Register() {
  
  let { userToken, setToken } = useContext(Login);
  let [Loding, setloading] = useState(false);
  let [errMesssag, setErorMessag] = useState("");
  let  navigate=useNavigate()
  setToken(null)
  async function RegisterForm(value) {
    setloading(false);
   
    setloading(true);
    let req = await axios
      .post("https://ecommerce.routemisr.com/api/v1/auth/signup", value)
      .catch(function (err) {
       
        setErorMessag ( err.response.data.message);
        setloading(false);

        console.log(errMesssag);
      });
    if (req?.data.message == "success") {
      setloading(true);
      console.log(req?.data.message)
     
      navigate("/Login")
    }
  }
  let validationSchema = yup.object({
    name: yup.string().required("Name Required").min(3, "min Char 3 ").max(20, "max Length 20"),
    email: yup.string().required("email Required").email("enter Valid Email"),
    phone: yup.string().required("phone Required").matches(/^01[1250][0-9]{8}$/, "enter valid Phone"),
    password: yup.string().matches(/^[A-Z][a-z0-9A-Z!@$%^&*()_-]{6,16}$/, "enter valid password").required("password Required"),
    rePassword: yup.string().oneOf([yup.ref("password")], "confirm Password not match").required("rePassword Required")
    
  });
  let x = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      phone: "",
    },
    onSubmit: RegisterForm,
    validationSchema,
  });

  return (
    <>
      <div>
        <h1>Register Now.......</h1>
        {errMesssag != "" ? (
          <div className="alert alert-danger">{errMesssag}</div>
        ) : (
          ""
        )}
        <form onSubmit={x.handleSubmit}>
          <div>
            <label htmlFor="name">name</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="text"
              className="form-control mb-3"
              id="name"
              name="name"
            />
            {x.errors.name && x.touched.name ? (
              <div className="alert alert-danger">{x.errors.name}</div>
            ) : (
              ""
            )}
          </div>
          <div>
            <label htmlFor="email"> Email</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="email"
              className="form-control mb-3"
              id="email"
              name="email"
            />
            {x.errors.email && x.touched.email ? (
              <div className="alert alert-danger">{x.errors.email}</div>
            ) : (
              ""
            )}
          </div>
          <div>
            <label htmlFor="password">password</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="password"
              className="form-control mb-3"
              id="password"
              name="password"
            />
            {x.errors.password && x.touched.password ? (
              <div className="alert alert-danger">{x.errors.password}</div>
            ) : (
              ""
            )}
          </div>
          <div>
            <label htmlFor="rePassword">repassword</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="password"
              className="form-control mb-3"
              id="rePassword"
              name="rePassword"
            />
            {x.errors.rePassword && x.touched.rePassword ? (
              <div className="alert alert-danger">{x.errors.rePassword}</div>
            ) : (
              ""
            )}
          </div>
          <div>
            <label htmlFor="phone">phone</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="tel"
              className="form-control mb-3"
              id="phone"
              name="phone"
            />
            {x.errors.phone && x.touched.phone ? (
              <div className="alert alert-danger">{x.errors.phone}</div>
            ) : (
              ""
            )}
          </div>
        </form>
        {Loding ? (
          <button className="btn bg-main text-white">
            <i className=" fa-spinner fa-spin fa-solid"></i>
          </button>
        ) : (
          <button
            type="submit"
            className="btn bg-main text-white "
            disabled={!(x.isValid && x.dirty)}
            onClick={x.handleSubmit}
          >
            {" "}
            submit
          </button>
        )}
      </div>
    </>
  );
}
