import React, { useContext, useEffect } from 'react'
import axios from 'axios'
import { useState } from 'react'
import { useQuery } from "react-query";
import { Login } from '../../ShareData/Login';
import { jwtDecode } from 'jwt-decode';



export default function Allorders() {
  let {userToken,setToken} =useContext(Login)
  let ID=jwtDecode(userToken)
  console.log(ID)

  function getAllBrands() {
    console.log();
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/orders/user/${ID.id}`
    );
  }
  let { isLoading, data, isError, isFetching, refetch } = useQuery(
    ["AllordersApi"],
    getAllBrands
  );
  console.log(data)
  return (
    <div className='container border-bottom p-3  '>
      <h1 className='text-center text-main'>All your orders</h1>
  {data?.data.map((item)=>{
return(
<div className='mt-5 '>
   <div className='bg-main d-flex rounded-1 justify-content-between container-fluid'>
   
     <h2 className=''>{"Total Price : "+item.totalOrderPrice+" EGP"}</h2>
     <h2 className=''>{item.user.name}</h2>
     </div> 
     {item.cartItems.map((cart)=>{
return(
     <table className='table-success w-100 container text-center'>


<thead>
<tr className='container bg-success '>

<th scope="col">count</th>
<th scope="col">name</th>
<th scope="col">price</th>
</tr>
</thead>
<tbody >
<tr className='container bg-success'>

<td style={{width:"33%"}}>{cart.count}</td>
<td style={{width:"33%"}}>{cart.product.title.split(" ").slice(0, 2).join(" ")}</td>
<td style={{width:"33%"}}>{(cart.price)*(cart.count)}</td>
</tr>


</tbody>

</table>

    )})} 
  
     </div>







)








  })}
  
 
      
       

     

     </div>
   )
 }
