import axios from "axios";
import React, { useContext, useState } from "react";
import { useQuery } from "react-query";
import MainSilder from "../MainSilder";
import CategorySlider from "../CategorySlider";
import { CartContext } from "../../ShareData/CartContext"
import toast, { Toaster } from "react-hot-toast";
import { Link } from "react-router-dom";

export default function Category() {

  let [page, setPage] = useState(1);
  function getAllCategory(queryParm) {
    console.log(queryParm);
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/categories?page=${queryParm.queryKey[1]}`
    );
  }
  let { isLoading, data, isError, isFetching, refetch } = useQuery(
    ["CategoryApi", page],
    getAllCategory
  );

  console.log(data);
 

  return (
    <>
      <div className="container py-5 ">
        <div className="row g-5">
          {data?.data.data.map((item) => {
            return (
              <>
                <div className="col-md-2 rounded-1 product" key={item.id}>
              

                  <img src={item.image} className="w-100 h-75" alt="" />
                 
                  <h6 className="text-black">
                    {item.name}
                  </h6>
                  
              
                </div>
              </>
            );
          })}
        </div>
        <nav aria-label="Page  navigation example">
          <ul className="pagination mt-5 justify-content-center">
            <li className="page-item">
              <a
                className="page-link"
                onClick={() => setPage(data?.data.metadata.currentPage - 1)}
                href="#"
                aria-label="Previous"
              >
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li className="page-item">
              <a className="page-link" onClick={() => setPage(1)}>
                1
              </a>
            </li>
            <li className="page-item">
              <a className="page-link" onClick={() => setPage(2)}>
                2
              </a>
            </li>

            <li className="page-item">
              <a
                className="page-link"
                href="#"
                onClick={() => setPage(data?.data.metadata.currentPage + 1)}
                aria-label="Next"
              >
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
}
