import axios from "axios";
import React, { useContext, useState } from "react";
import { useQuery } from "react-query";
import MainSilder from "../MainSilder";
import CategorySlider from "../CategorySlider";
import { CartContext } from "../../ShareData/CartContext"
import toast, { Toaster } from "react-hot-toast";
import { Link } from "react-router-dom";
import { WishListContext } from "../../ShareData/WishListContext";

export default function Home() {
  let { addCart ,setnumOfCartItems} = useContext(CartContext);
  let { addWishList ,removeWishItem} = useContext(WishListContext);

  let [page, setPage] = useState(1);

  function getAllProducts(queryParm) {
    console.log(queryParm);
    return axios.get(
      `https://ecommerce.routemisr.com/api/v1/products?page=${queryParm.queryKey[1]}`
    );
  }
  let { isLoading, data, isError, isFetching, refetch } = useQuery(
    ["productApi", page],
    getAllProducts
  );

 
  async function addToCart(id) {
    let req = await addCart(id);
    if (req.data.status == "success") {
      setnumOfCartItems(req?.data?.numOfCartItems);
      console.log()
      toast.success(req.data.message, {
        // position: 'bottom-right'
      });
    }
    
  }
  async function addToWishList(e,id) {
 
    let req = await addWishList(id);
    console.log(req);
    if (req.data.status == "success") {
      e.target.classList.replace('fa-regular','fa-solid')
    console.log( e.target.classList);
    
 
  }}
  

  return (
    <>
      <div className="container py-5 ">
        <MainSilder />
        <CategorySlider />
        <div className="row g-5">
          {data?.data.data.map((item) => {
            return (
              <>
                <div className="col-md-2 rounded-1 product position-relative" key={item.id}>
                   <i  className="fa-regular fa-heart position-absolute top-0 end-0 cursor-pointer  " onClick={(e)=>addToWishList(e,item.id)}></i>
                  
                <Link to={`/ProductDetails/${item.id}`}>

                  <img src={item.imageCover} className="w-100" alt="" />
                  <h6 className="text-main">{item.category.name}</h6>
                  <h6 className="text-black">
                    {item.title.split(" ").slice(0, 2).join(" ")}
                  </h6>
                  <div className="d-flex justify-content-between">
                    <h6>{item.price}EGP</h6>
                    <h6>
                      <i className="fa-solid fa-star rating-color"></i>
                      {item.ratingsAverage}
                    </h6>
                  </div>
                  </Link>
                  <div className="d-block">
                    <button className="btn bg-main text-white w-100" onClick={() => addToCart(item.id)}>
                      Add Cart
                    </button>
                  </div>
                </div>
              </>
            );
          })}
        </div>
        <nav aria-label="Page  navigation example">
          <ul className="pagination mt-5 justify-content-center">
            <li className="page-item">
              <a
                className="page-link"
                onClick={() => setPage(data?.data.metadata.currentPage - 1)}
                href="#"
                aria-label="Previous"
              >
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li className="page-item">
              <a className="page-link" onClick={() => setPage(1)}>
                1
              </a>
            </li>
            <li className="page-item">
              <a className="page-link" onClick={() => setPage(2)}>
                2
              </a>
            </li>

            <li className="page-item">
              <a
                className="page-link"
                href="#"
                onClick={() => setPage(data?.data.metadata.currentPage + 1)}
                aria-label="Next"
              >
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </>
  );
}
