import React, { useContext } from 'react'
import { WishListContext } from '../../ShareData/WishListContext';
import { useQuery } from 'react-query';
import axios from 'axios';
import { CartContext } from '../../ShareData/CartContext';
import { Link } from 'react-router-dom';

export default function WishListItems() {
   let{addToCart}=useContext(CartContext)
   let{removeWishItem}=useContext(WishListContext)
  
    function getAllWishList() {
        let options = {
            headers: {
                token: localStorage.getItem("userToken")
            }
        }
        return axios.get(
          `https://ecommerce.routemisr.com/api/v1/wishlist`,options 
        );
      }
      let { isLoading, data, isError, isFetching, refetch } = useQuery(
        ["productApi"],
        getAllWishList
      );
console.log(data);

async function remove(e,id) {
 
    let req = await removeWishItem(id);
    console.log(req);
    if (req.data.status == "success") {
      e.target.classList.replace('fa-solid','fa-regular')
    console.log( e.target.classList);
    
 
  }}

  return (
    
    <div className="container py-5 ">
    <div className="row g-5">
      {data?.data.data.map((item) => {
        return (
          <>
            <div className="col-md-2 rounded-1 product position-relative" key={item._id}>
               <i  className="fa-solid fa-heart position-absolute top-0 end-0 cursor-pointer  " onClick={(e)=>remove(e,item.id)}></i>
              
            <Link to={`/ProductDetails/${item.id}`}>

              <img src={item.imageCover} className="w-100" alt="" />
              <h6 className="text-main">{item.category.name}</h6>
              <h6 className="text-black">
                {item.title.split(" ").slice(0, 2).join(" ")}
              </h6>
              <div className="d-flex justify-content-between">
                <h6>{item.price}EGP</h6>
                <h6>
                  <i className="fa-solid fa-star rating-color"></i>
                  {item.ratingsAverage}
                </h6>
              </div>
              </Link>
              <div className="d-block">
                <button className="btn bg-main text-white w-100" onClick={() => addToCart(item.id)}>
                  Add Cart
                </button>
              </div>
            </div>
          </>
        );
      })}
    </div>
   
  </div>
     
  )

}
