import React, { useContext, useState } from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { Login } from "../../ShareData/Login";
export default function Register() {
  let [Loding, setloading] = useState(false);
  let [errMesssag, setErorMessag] = useState("");
  let {setToken}=useContext(Login)
  let  navigate=useNavigate()
  async function RegisterForm(value) {
    setloading(false);

    setloading(true);
    let req = await axios
      .post("https://ecommerce.routemisr.com/api/v1/auth/signin", value)
      .catch(function (err) {
        console.log(err)
    
        
        
        
        setErorMessag ( err.response.data.message);
        setloading(false);
        console.log(value);

        console.log(errMesssag);
      });
    if (req?.data.message == "success") {
      setloading(true);
      console.log(req?.data.message)
      setToken(req.data.token)
      localStorage.setItem("userToken", req.data.token)
      navigate("/Home")
    }
  }
  let validationSchema = yup.object({
    email: yup.string().required("required").email("valid email"),
    password: yup.string().required("required"),
  
  });
  let x = useFormik({
    initialValues: {
      
      email: "",
      password: ""
   
    },
    onSubmit: RegisterForm,
    validationSchema,
  });

  return (
    <>
      <div>
        <h1>Login Now.......</h1>
        {errMesssag != "" ? (
          <div className="alert alert-danger">{errMesssag}</div>
        ) : (
          ""
        )}
        <form onSubmit={x.handleSubmit}>
         
          <div>
            <label htmlFor="email"> Email</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="email"
              className="form-control mb-3"
              id="email"
              name="email"
            />
            {x.errors.email && x.touched.email ? (
              <div className="alert alert-danger">{x.errors.email}</div>
            ) : (
              ""
            )}
          </div>
          <div>
            <label htmlFor="password">password</label>
            <input
              onChange={x.handleChange}
              onBlur={x.handleBlur}
              type="password"
              className="form-control mb-3"
              id="password"
              name="password"
            />
            {x.errors.password && x.touched.password ? (
              <div className="alert alert-danger">{x.errors.password}</div>
            ) : (
              ""
            )}
          </div>
        
         
        </form>
        {Loding ? (
          <button className="btn bg-main text-white">
            <i className=" fa-spinner fa-spin fa-solid"></i>
          </button>
        ) : (
          <button
            type="submit"
            className="btn bg-main text-white "
            disabled={!(x.isValid && x.dirty)}
            onClick={x.handleSubmit}
          >
            {" "}
            submit
          </button>
        )}
           <Link to="/ForgetPassword">ForgetPassword ?.............</Link>
      </div>
    </>
  );
}
