import React, { useContext } from "react";
import { Link, NavLink, useNavigate } from "react-router-dom";
import logo from "../../finalProject assets/freshcart-logo.svg";
import { Login } from "../../ShareData/Login";
import { CartContext } from "../../ShareData/CartContext";

export default function Navbar() {
  let { numOfCartItems } = useContext(CartContext);
  let { userToken, setToken } = useContext(Login);
  let nag = useNavigate();
  function logOut() {
    setToken(null);
    localStorage.removeItem("userToken");
    console.log(userToken);
    nag("/login");
  }
 
  console.log(userToken);
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container-fluid px-3">
          <Link className="navbar-brand" to="home">
            <img src={logo} alt="" />
          </Link>
          <button
            class="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            {userToken == null ?  (
              ""
            ):(
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item">
                  <NavLink className="nav-link" to="Home">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                 
                  <NavLink className="nav-link" to="Cart">
                    Cart
                  </NavLink>
                </li>
                <li className="nav-item">
                 
                  <NavLink className="nav-link" to="Categories">
                    Categories
                  </NavLink>
                </li>
                <li className="nav-item">
               
                  <NavLink className="nav-link" to="WishList">
                    WishList
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="Brands">
                    Brands
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to="/allorders">
                    All Orders
                  </NavLink>
                </li>
              </ul>
            ) }

            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item d-flex align-items-center">
                <i className="fa-brands fa-facebook mx-1 cursor-pointer fs-4"></i>
                <i className="fa-brands fa-twitter mx-1 cursor-pointer fs-4"></i>
                <i className="fa-brands fa-spotify mx-1 cursor-pointer fs-4"></i>
                <i className="fa-brands fa-instagram mx-1 cursor-pointer fs-4"></i>
                {userToken != null ? (
                  <NavLink className="nav-link" to="Cart">
                    <span className="nav-link cursor-pointer position-relative">
                      <i className="fa-solid fa-cart-shopping"></i>

                      <span className="position-absolute top-0 start-100 translate-middle p-2 text-main border border-light rounded-circle">
                        {numOfCartItems}
                      </span>
                    </span>
                  </NavLink>
                ) : (
                  ""
                )}
              </li>
              {userToken != null ? (
                <>
                  {" "}
                  <li className="nav-item">
                    {" "}
                    <NavLink className="nav-link" to="" onClick={logOut}>
                      log out
                    </NavLink>{" "}
                  </li>{" "}
                </>
              ) : (
                <>
                  {" "}
                  <li className="nav-item">
                    <NavLink className="nav-link" to="Login">
                      Login
                    </NavLink>{" "}
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="">
                      Register
                    </NavLink>
                  </li>
                </>
              )}
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
